import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
	{
		path: '/',
		name: 'home',
		redirect: '/Checkout',
	},
	{
		path: '/Checkout',
		name: 'Checkout',
		component: () => import('../views/Checkout.vue'),
	},
	{
		path: '/*',
		name: '404',
		component: () => import('../views/404.vue'),
	},
]

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes,
})

export default router
